.hide {
  display: none;
}

.show {
  display: block;
}

.notifyPanel {
  flex: 0 1 auto;
}

.histogramSeries {
  flex-grow: 1;
}
.histogramSeries > div {
  justify-content: center;
  align-items: center;
}
.histogramSeries .arcgis-charts-modal-inside {
  padding: 1em;
  padding-left: 0;
}
.histogramSeries .arcgis-charts-modal-inside ol {
  padding-left: 2em;
}
.histogramSeries .arcgis-charts-modal-curtain {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 2001;
  background-color: white;
  opacity: 0.5;
  opacity: 1;
}
.histogramSeries .arcgis-charts-modal-content {
  background: rgba(255, 255, 255, 0.8);
  color: black;
  display: inline-block;
  position: absolute;
  top: 0px;
  left: 0px;
  max-width: 90%;
  max-height: 90%;
  overflow: auto;
  z-index: 2002;
  position: absolute;
  max-width: 90%;
  max-height: 90%;
  width: auto !important;
  height: auto !important;
  margin: auto !important;
  inset: auto !important;
  left: auto !important;
  top: auto !important;
}
.histogramSeries .arcgis-charts-modal-header {
  display: block;
  width: 100%;
  min-height: 1.8em;
  background: rgba(0, 0, 0, 0.05);
}
.histogramSeries .arcgis-charts-modal-close {
  display: block;
  position: absolute;
  top: 0.3em;
  right: 0.3em;
  background: url("data:image/svg+xml;charset=utf-8;base64,") center center/80% no-repeat rgba(100, 100, 100, 0.1);
  width: 1.2em;
  height: 1.2em;
  cursor: pointer;
}
.histogramSeries .arcgis-charts-modal-title {
  font-weight: bold;
  font-size: 110%;
  padding: 0.5em 1.2em 0.5em 1em;
}

:host {
  width: var(--arcgis-charts-histogram-width, var(--arcgis-charts-default-width));
  height: var(--arcgis-charts-histogram-height, var(--arcgis-charts-default-height));
  display: flex;
  flex-direction: row;
}